:root {
  --primary-color: #111;
  --secondary-color: #fff;
  --accent-color: #fd9222;
  --dark-background-color: #1e1823;
  scroll-behavior: smooth;
  --swiper-theme-color: #fd9222 !important;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

.comment-textarea, img {
  max-width: 100%;
  display: block;
}

.list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

.link {
  text-decoration: none;
}

button {
  cursor: pointer;
}

body {
  background-color: #fafafa;
  font-family: Montserrat, sans-serif;
}

.accent-color {
  color: var(--accent-color);
}

.container {
  min-width: 285px;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 16px;
}

.section-title {
  letter-spacing: -.03em;
  text-align: center;
  text-transform: uppercase;
  color: var(--primary-color);
  max-width: 412px;
  margin-bottom: 32px;
  margin-left: auto;
  margin-right: auto;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.17;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.header-cont {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
}

.page-navigation {
  width: 100%;
  max-width: 898px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logotype {
  letter-spacing: -.03em;
  color: var(--primary-color);
  text-transform: uppercase;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.11111;
  display: flex;
}

.logo-icon {
  margin-left: 4px;
}

.menu {
  column-gap: 24px;
  display: flex;
}

.menu-link {
  letter-spacing: -.02em;
  color: var(--primary-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.footer-nav-link:hover, .footer-nav-link:focus, .menu-link:hover, .menu-link:focus {
